<template>
  <header class="header d-flex align-items-center justify-content-between px-3 w-100">
    <div class="header__mob d-block d-lg-none">
      <BrgBtn
        :state="visibilityMobile" 
        @click="changeVisibility"
      />
      <ul
        v-if="visibilityMobile" 
        class="header__links-mob d-flex flex-column align-items-center"
      >
        <li 
          v-for="page in pagesList" 
          :key="page.name" 
          class="header__link p-3"
        >
          <router-link class="router-link" :to='page.link'>
            {{page.name}}
          </router-link>
        </li>
      </ul>
    </div>
    <img class="header__logo" src="@/assets/img/logo.png" width="150" />
    <ul class="header__links d-none d-lg-flex">
      <li 
        v-for="page in pagesList"
        :class="{'active': $route.fullPath == page.link}"
        :key="page.name" 
        class="header__link"
      >
        <router-link
          class="router-link"
          :to='page.link'
        >
          {{page.name}}
        </router-link>
      </li>
    </ul>
    <div class="header__user">
      <div 
        @click="popUpVisible = !popUpVisible"
        data-popup='true'
        class="header__user-inner d-flex pointer align-items-center  mr-lg-5"
      >
        <span 
          class="header__user-title user-none"
          data-popup='true'
        >Гость</span>
        <img
          data-popup='true'
          src="http://via.placeholder.com/64x64" 
          class="rounded-circle ml-2 pointer" 
          width="32" 
          alt=""
        >
        <CangeLanguage />
      </div>
      <PopUp
        @close="popUpVisible=false"
        :visible="popUpVisible"
      >
        <span
          @click="toggleModal('logModal')"
          class="popUp__item pointer"
        > 
          <i class="fas fa-power-off mr-2"></i> Войти 
        </span>
        <span
          @click="toggleModal('regModal')"
          class="popUp__item pointer"
        > 
          <i class="fas fa-power-off mr-2"></i> Регистрация 
        </span>
      </PopUp>
    </div>

    <transition name="modal">
      <Modal 
        v-if="regModal"
        @close="toggleModal('regModal')"
      >
        <div class="registration__modal">
          <img
            src="@/assets/img/logo.png"
            width="140"
            style="margin: 0 auto 10px;"
            alt=""
          >
          <p class="text-center mb-5">Turn your on your business</p>
          <div class="form-group">
            <label></label>
            <input 
              type="text"
              class="form-control"
              id="name"
              placeholder="Укажите Ваше имя"
            >
          </div>
          <div class="form-group">
            <input 
              type="text" 
              class="form-control" 
              id="email" 
              placeholder="Укажите электронную почту"
            >
          </div>
          <div class="form-group">
            <input 
              type="password" 
              class="form-control" 
              id="password"
              placeholder="Укажите пароль"
            >
          </div>
          <div class="form-group">
            <input 
              type="password" 
              class="form-control" 
              id="repeat-password" 
              placeholder="Повторите пароль"
            >
          </div>
          <div class="form-group">
            <input 
              type="text" 
              class="form-control" 
              id="phone" 
              placeholder="Укажите Ваш телефон"
            >
          </div>
          <div 
            class="d-flex justify-content-between align-items-center my-2"
          >
            <label 
              class="d-block"
            >
              Акаунт для бізнесу
            </label>
            <CheckBox />
          </div>
          <div class="text-center my-3">
            При нажатии на кнопку регистрации Вы соглашаетесь с&nbsp;<a href="#">пользовательски соглашением</a>
          </div>
          <button
            @click="signup"
            type="submit" 
            class="btn btn-primary btn-block" id="reg-form-btn"
          >
            Регистрация
          </button>
        </div>
      </Modal>

      <Modal 
        v-if="logModal"
        @close="toggleModal('logModal')"
      >
        <div class="login__modal row">
          <div
            class="col-6 bg-log d-none d-lg-block" 
            style="width:900px;"
          >
            <div style="padding: 40px;">
              <h4 class="title__modal mb-4">Fine.Trading</h4>
              <p class="op-07">
                Obfuscate, simplify, uglify, white power.<br> We are watching you, scum
              </p>
              <p class="mt-5">
                <span
                  style="font-weight: 500; font-size: 13px;" 
                  class="d-block mb-3"
                >
                  OUR ADRESS:
                </span>
                <span class="op-07">Hell, street 666</span>
              </p>
            </div>
          </div>
          <div
            class="col-lg-6 col-12"
          >
            <div style="padding: 30px;" class=" d-flex flex-column align-items-center">
              <h3 
                style="color:#343a40;"
                class="title__modal mb-lg-1 mb-3"
              >
                Welcome back!
              </h3>
              <p>Sign in to your account to continue, or get lost</p>
              <p style="font-size:1rem;" class="text-center text-danger mt-4" v-if="errorMessage">
                {{this.errorMessage}}
              </p>
              <div class="form-group mb-4 mt-2">
                <label>
                  <input
                    v-model="logForm.email"
                    type="email" 
                    id="email" 
                    class="form-control py-2" 
                    placeholder=""
                  >
                </label>
              </div>
              <div class="form-group mb-4">
                <label>
                  <input 
                    v-model="logForm.password"
                    type="password" 
                    id="pass" 
                    class="form-control py-2" 
                    placeholder=""
                  >
                </label>
              </div>
              <button 
                @click="login"
                :disabled="loading"
                class="btn w-100 bg-log"
                id="loginFormBtn"
              >
                <p v-if="!loading">Войти</p>
                <img v-else src="@/assets/img/loading.svg" width="30px" alt="">
              </button>
              <p class="my-4">Feeling helpless? No one wants to communicate with you? <a href="">Sign Up</a>, or piss off</p>
            </div>
          </div>
            
        </div>
      </Modal>
    </transition>

  </header>
</template>

<script>

import { mapState, mapActions } from 'vuex'

const pagesList = [
  {
    link: '/',
    name: 'Можливостi'
  },
    {
    link: '/instructions',
    name: 'Iнструкцiя'
  },
  {
    link: '/for_whom',
    name: 'Для кого'
  },
    {
    link: '/tariffs',
    name: 'Тарифи'
  },
]

const defLogForm = () => ({
  email: 'bus@bus',
  password: '123'
})

export default {
  name: 'Header',
  data() {
    return {
      pagesList: pagesList,
      popUpVisible: false,
      visibilityMobile: false,
      regModal: false,
      logModal: false,
      errorMessage: '',
      logForm: defLogForm()
    }
  },
  components: {
    PopUp: () => import('@/components/popUp'),
    BrgBtn: () => import('@/components/brgBtn'),
    Modal: () => import('@/components/layout/modal'),
    CheckBox: () => import('@/components/general/checkSlide'),
    CangeLanguage: () => import('@/components/general/changelocal')
  },
  computed: {
    ...mapState([
      'userInfo',
      'loading'
    ])
  },
  methods: {
    ...mapActions([
      'LOGIN',
      'CHECK_USER'
    ]),
    toggleModal(value) {
      this[value] = !this[value]
    },
    changeVisibility() {
      this.visibilityMobile = !this.visibilityMobile
    },
    async login() {
      if( await this.LOGIN(this.logForm) ) {
        this.$router.push('/dashboard')
        this.errorMessage = ''
      }else {
        this.errorMessage = "Введены неверные данные"
      }
    },
    signup() {
      this.$router.push('/dashboard').catch(() => {
        console.log('')
      })
    }
  }
}
</script>

<style lang="scss">
 @import '@/assets/styles/utils/vars.scss';
 $modalLogColor: #0866C6;

  .header {
    position: fixed;
    z-index: 1500;
    background: $border-light;
    &__user-icon {
      width: 45px;
      height: 45px;
      background: $light-color;
      border-radius: 50px;
    }
    &__mob {
      position: relative;
    }
    &__links-mob{
      position: absolute;
      top: 50px;
      border: 1px solid $neutral-placeholder;
      background: #fff;
    }
  }
  .header__logo {
    padding: 10px 10px 10px 50px;
  }

  .header__link {
    padding: 28px 2rem 20px;
    margin: auto 0;
    a {
      color: #868e96;
      position: relative;
      transition: all 0.4s ease-in-out;

      &:hover {
        color: #343a40;
      }

      &::before {
        content: '';
        position: absolute;
        opacity: 0;
        top: 35px;
        width: 100%;
        background: #0866C6;
        height: 3px;
        transition: all 0.4s ease-in-out;
      }
    }
  }

  .header__link.active {
    a {
      color: #0866C6;

      &::before { opacity: 1; }
    }
  }

  .header__user {
    position: relative;
  }

  //modals

  .registration__modal {
    display: flex;
    flex-direction: column;
    padding: 2rem;
  }

  .title__modal { font-weight: 600; font-size: 1.5rem;}

  .bg-log { background: $modalLogColor; color: #fff;}

</style>
